import React from 'react';
import PropTypes from 'prop-types';
import connect from '@vkontakte/vkui-connect';
import { Panel, ListItem, Button, Group, Div, Avatar, PanelHeader, ConfigProvider, View } from '@vkontakte/vkui';




const Install = ({ id, installApp, fetchedUser }) => {
    
	
    // console.log('location.href', window.location.href);
    var parsedUrl = new URL(window.location.href);
    // console.log(parsedUrl);
    // console.log(parsedUrl.searchParams.get("vk_app_id"));
    let vkappid = parsedUrl.searchParams.get("vk_app_id");
    let vkgroupid = parsedUrl.searchParams.get("vk_group_id");
    // console.log(vkgroupid);
    // console.log(obj)

    installApp = (e) => {
			console.log('Тут Апп', e )
            // window.open(`https://vk.com/add_community_app?aid=${vkappid}`);
            connect.send("VKWebAppAddToCommunity", {});
		
	};

    return(
<ConfigProvider isWebView={true}>
<View>
<Panel id='home'>
		<PanelHeader>Магнит Подписчиков</PanelHeader>
		{fetchedUser &&
		<Group title="Добро Пожаловать в Приложение">
			<ListItem
				before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}/> : null}
				description={fetchedUser.city && fetchedUser.city.title ? fetchedUser.city.title : ''}
			>
				{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
			</ListItem>
        </Group>}

        {vkgroupid &&
		<Group title="Приложение стоит в группе">
			<ListItem>
                
				{`${vkgroupid} `}
			</ListItem>
		</Group>}

        {vkgroupid == null &&
		<Group title="Меню">
			<Div>
				<Button size="xl" level="2" onClick={installApp} data-to="persik">
					Установить в Сообщество
				</Button>
			</Div>
		</Group>}
	</Panel>

</View>
</ConfigProvider>
	




)};

Install.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Install;
